html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strong,
sub,
sup,
var,
b,
u,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  border: 0;
  font: inherit;
  font-size: 100%;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
/*
 * Font Guide: To get [Font Name] use the corresponding rules.
 *
 * Proxima Nova Black
 * font-family: proxima-nova, sans-serif;
 * font-weight: 900;
 * font-style: normal;
 *
 * Proxima Nova Extrabold
 * font-family: proxima-nova, sans-serif;
 * font-weight: 800;
 * font-style: normal;
 *
 * Proxima Nova Regular
 * font-family: proxima-nova, sans-serif;
 * font-weight: 400;
 * font-style: normal;
 *
 * Proxima Nova Italic
 * font-family: proxima-nova, sans-serif;
 * font-weight: 400;
 * font-style: italic;
 *
 * Proxima Nova Light
 * font-family: proxima-nova, sans-serif;
 * font-weight: 300;
 * font-style: normal;
 *
 * Proxima Nova Light Italic
 * font-family: proxima-nova, sans-serif;
 * font-weight: 300;
 * font-style: italic;
 *
 * Proxima Nova Medium Italic
 * font-family: proxima-nova, sans-serif;
 * font-weight: 500;
 * font-style: italic;
 */
html {
  color: rgba(0, 0, 0, 0.87);
  font-family: proxima-nova, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
}
body {
  color: rgba(0, 0, 0, 0.87);
  font-size: 1.6rem;
}
h1 {
  color: rgba(0, 0, 0, 0.87);
  font-family: proxima-nova, sans-serif;
  font-size: 9rem;
  font-style: normal;
  font-weight: 300;
  line-height: 9rem;
}
h2 {
  color: rgba(0, 0, 0, 0.87);
  font-family: proxima-nova, sans-serif;
  font-size: 6.7rem;
  font-style: normal;
  font-weight: 300;
  line-height: 6.2rem;
}
h3 {
  color: rgba(0, 0, 0, 0.87);
  font-family: proxima-nova, sans-serif;
  font-size: 5.1rem;
  font-style: normal;
  font-weight: 300;
  line-height: 5.1rem;
}
h4 {
  color: rgba(0, 0, 0, 0.87);
  font-family: proxima-nova, sans-serif;
  font-size: 3.9rem;
  font-style: normal;
  font-weight: 800;
  line-height: 3.9rem;
}
h5 {
  color: rgba(0, 0, 0, 0.87);
  font-family: proxima-nova, sans-serif;
  font-size: 2.8rem;
  font-style: normal;
  font-weight: 800;
  line-height: 2.8rem;
}
h6 {
  color: rgba(0, 0, 0, 0.87);
  font-family: proxima-nova, sans-serif;
  font-size: 2.1rem;
  font-style: normal;
  font-weight: 800;
  line-height: 2.1rem;
}
strong {
  font-family: proxima-nova, sans-serif;
  font-weight: 800;
  font-style: normal;
}
p,
span {
  font-family: proxima-nova, sans-serif;
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 400;
  line-height: 2.2rem;
}
p.small,
span.small {
  font-size: 1.4rem;
  line-height: 2.2rem;
}
p.large,
span.large {
  font-size: 1.9rem;
  line-height: 2.6rem;
}
blockquote {
  font-family: proxima-nova, sans-serif;
  font-size: 1.6rem;
  font-style: italic;
  font-weight: 400;
  line-height: 2.8rem;
}
ul,
ol {
  font-family: proxima-nova, sans-serif;
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 400;
  line-height: 2.2rem;
}
.subheading {
  color: rgba(0, 0, 0, 0.6);
  font-family: proxima-nova, sans-serif;
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 800;
}
.subheading.small {
  font-size: 1.2rem;
  text-transform: uppercase;
}
.hero {
  color: rgba(0, 0, 0, 0.87);
  font-family: proxima-nova, sans-serif;
  font-size: 12rem;
  font-style: normal;
  font-weight: 900;
  line-height: 10rem;
}
.hero.large {
  font-size: 15rem;
  line-height: 12rem;
}
.pull-quote {
  font-family: proxima-nova, sans-serif;
  font-size: 2.8rem;
  font-style: italic;
  font-weight: 500;
  line-height: 4rem;
}
.caption {
  font-family: proxima-nova, sans-serif;
  font-size: 2.1rem;
  font-style: italic;
  font-weight: 300;
  line-height: 2.2rem;
}
.caption.small {
  font-size: 1.6rem;
}
.primary-text {
  color: rgba(0, 0, 0, 0.87);
}
.primary-text.inverted {
  color: #ffffff;
}
.secondary-text {
  color: rgba(0, 0, 0, 0.6);
}
.secondary-text.inverted {
  color: rgba(255, 255, 255, 0.7);
}
.help-text {
  color: rgba(0, 0, 0, 0.38);
}
.help-text.inverted {
  color: rgba(255, 255, 255, 0.5);
}
/* Grayscale
 * #fff - #000 in [various]% steps
 * See https://talloux.invisionapp.com/console/Design-System-ck70x3ivn2cy2017xjpu68gy4/ck70x4duz0046013ojf58m5ig/inspect
 */
.ce-table {
  --initial-background: transparent;
  display: flex;
}
@media screen and (min-width: 765px) {
  .ce-table {
    flex-direction: column;
  }
}
.ce-table .ce-table__header {
  box-sizing: border-box;
}
@media screen and (min-width: 765px) {
  .ce-table .ce-table__header {
    border-bottom: 0.1rem solid #bfbfbf;
    display: flex;
    flex-direction: column;
  }
  .ce-table .ce-table__header .ce-table__header--group {
    align-items: center;
    display: flex;
    height: 5rem;
    padding-left: 2.1rem;
  }
  .ce-table .ce-table__header .ce-table__header--group .ce-table__heading {
    flex: 1 1 0px;
    font-size: 1.4rem;
    font-weight: bold;
    line-height: 1.9rem;
  }
  .ce-table .ce-table__header .ce-table__header--group .ce-table__heading:first-of-type {
    display: none;
  }
}
@media screen and (max-width: 765px) {
  .ce-table .ce-table__header {
    display: none;
  }
}
@media screen and (max-width: 765px) {
  .ce-table .ce-table__body {
    flex: 1 1 0px;
  }
}
.ce-table .ce-table__body .ce-table__row {
  background-color: var(--background-color, var(--initial-background));
  display: flex;
}
@media screen and (min-width: 765px) {
  .ce-table .ce-table__body .ce-table__row {
    align-items: center;
    height: 5rem;
    padding-left: 2.1rem;
  }
}
@media screen and (max-width: 765px) {
  .ce-table .ce-table__body .ce-table__row {
    flex-direction: column;
    padding-left: 0.8rem;
  }
}
@media screen and (min-width: 765px) {
  .ce-table .ce-table__body .ce-table__row .ce-table__cell {
    flex: 1 1 0px;
  }
}
@media screen and (max-width: 765px) {
  .ce-table .ce-table__body .ce-table__row .ce-table__cell {
    display: flex;
    flex-direction: row;
  }
}
@media screen and (min-width: 765px) {
  .ce-table .ce-table__body .ce-table__row .ce-table__cell .ce-table__cell-heading {
    display: none;
  }
}
@media screen and (max-width: 765px) {
  .ce-table .ce-table__body .ce-table__row .ce-table__cell .ce-table__cell-heading {
    flex: 1 1 0px;
    font-size: 1.4rem;
    font-weight: bold;
    line-height: 2.8rem;
  }
}
.ce-table .ce-table__body .ce-table__row .ce-table__cell .ce-table__cell-body {
  font-size: 1.4rem;
}
@media screen and (max-width: 765px) {
  .ce-table .ce-table__body .ce-table__row .ce-table__cell .ce-table__cell-body {
    flex: 1 1 0px;
    line-height: 2.8rem;
  }
}
.ce-table .ce-table__body .ce-table__row .ce-table__cell:first-of-type {
  display: none;
}
.ce-table .ce-table__body .ce-table__row:nth-child(odd) {
  --background-color: #f1f6f9;
}
.ce-table .ce-table__body .ce-table__row:nth-child(even) {
  --background-color: #fff;
}
@media screen and (max-width: 765px) {
  .ce-table .ce-table__body .ce-table__row:nth-child(even) {
    border: 0.1rem solid #e8e8e8;
  }
}
.ce-table.ce-table__clickable .ce-table__row {
  cursor: pointer;
}
.ce-table.ce-table__clickable .ce-table__row:hover {
  --background-color: #e6f7ff;
}
.ce-table.ce-table__selectable .ce-checkbox input[type='checkbox']:hover {
  border-color: #b2b2b2;
}
.ce-table.ce-table__selectable .ce-checkbox input[type='checkbox']:checked:hover {
  border-color: #192344;
}
.ce-table.ce-table__selectable .ce-checkbox label {
  display: none;
}
@media screen and (min-width: 765px) {
  .ce-table.ce-table__selectable .ce-table__header .ce-table__header--group {
    padding-left: 0;
  }
}
@media screen and (min-width: 765px) {
  .ce-table.ce-table__selectable .ce-table__header .ce-table__header--group .ce-table__heading:first-of-type {
    box-sizing: border-box;
    display: block;
    max-width: 5rem;
    padding-left: 0.8rem;
  }
}
@media screen and (min-width: 765px) {
  .ce-table.ce-table__selectable .ce-table__header .ce-table__header--group .ce-table__heading:not(:first-of-type) {
    padding-left: 2.5rem;
  }
}
.ce-table.ce-table__selectable .ce-table__body .ce-table__row {
  cursor: pointer;
}
@media screen and (min-width: 765px) {
  .ce-table.ce-table__selectable .ce-table__body .ce-table__row {
    padding-left: 0;
  }
}
@media screen and (max-width: 765px) {
  .ce-table.ce-table__selectable .ce-table__body .ce-table__row {
    padding-left: 0;
  }
}
@media screen and (min-width: 765px) {
  .ce-table.ce-table__selectable .ce-table__body .ce-table__row .ce-table__cell:first-of-type {
    box-sizing: border-box;
    display: block;
    max-width: 5rem;
    padding-left: 0.8rem;
  }
}
@media screen and (max-width: 765px) {
  .ce-table.ce-table__selectable .ce-table__body .ce-table__row .ce-table__cell:first-of-type {
    max-height: 4.3rem;
    display: flex;
  }
}
.ce-table.ce-table__selectable .ce-table__body .ce-table__row .ce-table__cell:first-of-type .ce-table__cell-heading {
  box-sizing: border-box;
  max-height: 4.3rem;
  padding-left: 0.3rem;
  padding-top: 0.3rem;
}
@media screen and (max-width: 765px) {
  .ce-table.ce-table__selectable .ce-table__body .ce-table__row .ce-table__cell:first-of-type .ce-table__cell-heading {
    display: none;
  }
}
@media screen and (max-width: 765px) {
  .ce-table.ce-table__selectable .ce-table__body .ce-table__row .ce-table__cell:first-of-type .ce-table__cell-body {
    padding-left: 0.3rem;
    padding-top: 0.3rem;
  }
}
@media screen and (max-width: 765px) {
  .ce-table.ce-table__selectable .ce-table__body .ce-table__row .ce-table__cell:last-of-type {
    padding-bottom: 2.2rem;
  }
}
@media screen and (min-width: 765px) {
  .ce-table.ce-table__selectable .ce-table__body .ce-table__row .ce-table__cell:not(:first-of-type) {
    padding-left: 2.5rem;
  }
}
@media screen and (max-width: 765px) {
  .ce-table.ce-table__selectable .ce-table__body .ce-table__row .ce-table__cell:not(:first-of-type) {
    padding-left: 5rem;
  }
}
.ce-table.ce-table__selectable .ce-table__body .ce-table__row.ce-table__row--selected {
  --background-color: #ccefff;
}
.ce-table.ce-table__selectable .ce-table__body .ce-table__row:hover {
  --background-color: #e6f7ff;
}
