    --initial-background: #fbfbfb;
    --initial-border: #b2b2b2;
    --initial-channel: #7f7f7f;
    --initial-disc: transparent;
    --initial-disc-hover: #e5e5e5;
    --initial-disc-active: #b3b3b3;
    --initial-disc-focus: #ccc;
    --checked-background: #fff;
    --checked-border: #192344;
    --checked-indicator: #192344;
    --checked-channel: #007ab2;
    --checked-disc-hover: #e6f7ff;
    --checked-disc-active: #b2e7ff;
    --checked-disc-focus: #ccefff;
    --disabled-unchecked-border: #dbdbdb;
    --disabled-unchecked-channel: #e5e5e5;
    --disabled-checked-border: #aeaeae;
    --disabled-switch-indicator: #99dfff;
    --disabled-checked-channel: #ccefff;
    --disabled-checked-indicator: #aeaeae;
    -webkit-appearance: none;
    -moz-appearance: none;
    background: var(--background, var(--initial-background));
    border: 1px solid var(--border, var(--initial-border));
    cursor: pointer;
    display: inline-block;
    height: 20px;
    margin: 0;
    outline: none;
    position: relative;
    vertical-align: top;
  }
  .ce-checkbox input[type='checkbox']:before,
  .ce-radio input[type='checkbox']:before,
  .ce-switch input[type='checkbox']:before,
  .ce-checkbox input[type='radio']:before,
  .ce-radio input[type='radio']:before,
  .ce-switch input[type='radio']:before {
    background: var(--disc, var(--initial-disc));
    border: 18px solid transparent;
    border-radius: 50%;
    content: '';
    display: inline-block;
    height: 0px;
    margin: -9px;
    position: relative;
    width: 0px;
    z-index: -1;
  }
  .ce-checkbox input[type='checkbox']:after,
  .ce-radio input[type='checkbox']:after,
  .ce-switch input[type='checkbox']:after,
  .ce-checkbox input[type='radio']:after,
  .ce-radio input[type='radio']:after,
  .ce-switch input[type='radio']:after {
