  .ce-checkbox input[type='checkbox']:checked,
  .ce-radio input[type='checkbox']:checked,
  .ce-switch input[type='checkbox']:checked,
  .ce-checkbox input[type='radio']:checked,
  .ce-radio input[type='radio']:checked,
  .ce-switch input[type='radio']:checked {
    --background: var(--checked-background);
    --border: var(--checked-border);
  }
  .ce-checkbox input[type='checkbox']:checked:after,
  .ce-radio input[type='checkbox']:checked:after,
  .ce-switch input[type='checkbox']:checked:after,
  .ce-checkbox input[type='radio']:checked:after,
