html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strong,
sub,
sup,
var,
b,
u,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  border: 0;
  font: inherit;
  font-size: 100%;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
/*
 * Font Guide: To get [Font Name] use the corresponding rules.
 *
 * Proxima Nova Black
 * font-family: proxima-nova, sans-serif;
 * font-weight: 900;
 * font-style: normal;
 *
 * Proxima Nova Extrabold
 * font-family: proxima-nova, sans-serif;
 * font-weight: 800;
 * font-style: normal;
 *
 * Proxima Nova Regular
 * font-family: proxima-nova, sans-serif;
 * font-weight: 400;
 * font-style: normal;
 *
 * Proxima Nova Italic
 * font-family: proxima-nova, sans-serif;
 * font-weight: 400;
 * font-style: italic;
 *
 * Proxima Nova Light
 * font-family: proxima-nova, sans-serif;
 * font-weight: 300;
 * font-style: normal;
 *
 * Proxima Nova Light Italic
 * font-family: proxima-nova, sans-serif;
 * font-weight: 300;
 * font-style: italic;
 *
 * Proxima Nova Medium Italic
 * font-family: proxima-nova, sans-serif;
 * font-weight: 500;
 * font-style: italic;
 */
html {
  color: rgba(0, 0, 0, 0.87);
  font-family: proxima-nova, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
}
body {
  color: rgba(0, 0, 0, 0.87);
  font-size: 1.6rem;
}
h1 {
  color: rgba(0, 0, 0, 0.87);
  font-family: proxima-nova, sans-serif;
  font-size: 9rem;
  font-style: normal;
  font-weight: 300;
  line-height: 9rem;
}
h2 {
  color: rgba(0, 0, 0, 0.87);
  font-family: proxima-nova, sans-serif;
  font-size: 6.7rem;
  font-style: normal;
  font-weight: 300;
  line-height: 6.2rem;
}
h3 {
  color: rgba(0, 0, 0, 0.87);
  font-family: proxima-nova, sans-serif;
  font-size: 5.1rem;
  font-style: normal;
  font-weight: 300;
  line-height: 5.1rem;
}
h4 {
  color: rgba(0, 0, 0, 0.87);
  font-family: proxima-nova, sans-serif;
  font-size: 3.9rem;
  font-style: normal;
  font-weight: 800;
  line-height: 3.9rem;
}
h5 {
  color: rgba(0, 0, 0, 0.87);
  font-family: proxima-nova, sans-serif;
  font-size: 2.8rem;
  font-style: normal;
  font-weight: 800;
  line-height: 2.8rem;
}
h6 {
  color: rgba(0, 0, 0, 0.87);
  font-family: proxima-nova, sans-serif;
  font-size: 2.1rem;
  font-style: normal;
  font-weight: 800;
  line-height: 2.1rem;
}
strong {
  font-family: proxima-nova, sans-serif;
  font-weight: 800;
  font-style: normal;
}
p,
span {
  font-family: proxima-nova, sans-serif;
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 400;
  line-height: 2.2rem;
}
p.small,
span.small {
  font-size: 1.4rem;
  line-height: 2.2rem;
}
p.large,
span.large {
  font-size: 1.9rem;
  line-height: 2.6rem;
}
blockquote {
  font-family: proxima-nova, sans-serif;
  font-size: 1.6rem;
  font-style: italic;
  font-weight: 400;
  line-height: 2.8rem;
}
ul,
ol {
  font-family: proxima-nova, sans-serif;
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 400;
  line-height: 2.2rem;
}
.subheading {
  color: rgba(0, 0, 0, 0.6);
  font-family: proxima-nova, sans-serif;
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 800;
}
.subheading.small {
  font-size: 1.2rem;
  text-transform: uppercase;
}
.hero {
  color: rgba(0, 0, 0, 0.87);
  font-family: proxima-nova, sans-serif;
  font-size: 12rem;
  font-style: normal;
  font-weight: 900;
  line-height: 10rem;
}
.hero.large {
  font-size: 15rem;
  line-height: 12rem;
}
.pull-quote {
  font-family: proxima-nova, sans-serif;
  font-size: 2.8rem;
  font-style: italic;
  font-weight: 500;
  line-height: 4rem;
}
.caption {
  font-family: proxima-nova, sans-serif;
  font-size: 2.1rem;
  font-style: italic;
  font-weight: 300;
  line-height: 2.2rem;
}
.caption.small {
  font-size: 1.6rem;
}
.primary-text {
  color: rgba(0, 0, 0, 0.87);
}
.primary-text.inverted {
  color: #ffffff;
}
.secondary-text {
  color: rgba(0, 0, 0, 0.6);
}
.secondary-text.inverted {
  color: rgba(255, 255, 255, 0.7);
}
.help-text {
  color: rgba(0, 0, 0, 0.38);
}
.help-text.inverted {
  color: rgba(255, 255, 255, 0.5);
}
/* Grayscale
 * #fff - #000 in [various]% steps
 * See https://talloux.invisionapp.com/console/Design-System-ck70x3ivn2cy2017xjpu68gy4/ck70x4duz0046013ojf58m5ig/inspect
 */
.ce-input label {
  color: rgba(0, 0, 0, 0.6);
  display: block;
  font-size: 1.6rem;
  line-height: 2.2rem;
  margin: 0.3rem 0;
}
.ce-input .ce-input__help-text {
  color: rgba(0, 0, 0, 0.38);
  display: block;
  font-size: 1.4rem;
  line-height: 1.6rem;
  margin: 0.3rem 0;
}
.ce-input .ce-input__container {
  background-color: #fbfbfb;
  border: 0.1rem solid #b2b2b2;
  border-radius: 0.5rem;
  box-sizing: border-box;
  display: flex;
  height: 6rem;
  justify-content: space-between;
  margin: 0.3rem 0;
  width: calc(100% - 0.6rem);
}
.ce-input .ce-input__container .ce-input__icon {
  align-self: center;
  padding-left: 1rem;
}
.ce-input .ce-input__container .ce-input__icon span,
.ce-input .ce-input__container .ce-input__icon svg {
  display: inline-block;
  height: 1.6rem;
  width: 1.6rem;
}
.ce-input .ce-input__container .ce-input__icon svg {
  stroke: rgba(0, 0, 0, 0.87);
}
.ce-input .ce-input__container input {
  background-color: transparent;
  border: none;
  border-radius: 0.5rem;
  box-sizing: border-box;
  color: rgba(0, 0, 0, 0.87);
  height: 100%;
  flex-grow: 1;
  font-family: proxima-nova, sans-serif;
  font-size: 1.4rem;
  font-style: normal;
  font-weight: 400;
  line-height: 2rem;
  padding: 0 0 0 1rem;
}
.ce-input .ce-input__container input:focus {
  outline: none;
}
.ce-input .ce-input__container input[type="month"]::-webkit-calendar-picker-indicator,
.ce-input .ce-input__container input[type="date"]::-webkit-calendar-picker-indicator,
.ce-input .ce-input__container input[type="time"]::-webkit-calendar-picker-indicator {
  background: none;
}
.ce-input .ce-input__container .ce-input__date-icon {
  align-self: center;
  padding-right: 2rem;
  stroke: rgba(0, 0, 0, 0.87);
}
.ce-input .ce-input__container .ce-input__button {
  background: linear-gradient(317.58deg, #6645ad 0%, #2d71be 100%);
  border: none;
  border-radius: 0 0.5rem 0.5rem 0;
  height: 6rem;
  margin-top: -0.1rem;
  padding: 0 2.2rem;
}
.ce-input .ce-input__container .ce-input__button span,
.ce-input .ce-input__container .ce-input__button svg {
  stroke: #ffffff;
  display: inline-block;
  height: 1.6rem;
  width: 1.6rem;
}
.ce-input .ce-input__container:focus {
  box-shadow: -0.3rem 0 #ccefff, 0.3rem 0 #ccefff, 0 -0.3rem #ccefff, 0 0.3rem #ccefff;
  outline: none;
}
.ce-input .ce-input__container:hover {
  box-shadow: -0.3rem 0 #e6f7ff, 0.3rem 0 #e6f7ff, 0 -0.3rem #e6f7ff, 0 0.3rem #e6f7ff;
}
.ce-input .ce-input__container:active {
  box-shadow: -0.3rem 0 #b2e7ff, 0.3rem 0 #b2e7ff, 0 -0.3rem #b2e7ff, 0 0.3rem #b2e7ff;
}
.ce-input.ce-input--hidden {
  border: none;
  display: none;
  height: 0;
  width: 0;
}
.ce-input.ce-input--disabled label {
  color: rgba(0, 0, 0, 0.38);
}
.ce-input.ce-input--disabled .ce-input__help-text {
  color: rgba(0, 0, 0, 0.38);
}
.ce-input.ce-input--disabled .ce-input__container .ce-input__icon svg {
  stroke: rgba(0, 0, 0, 0.38);
}
.ce-input.ce-input--disabled .ce-input__container input {
  color: rgba(0, 0, 0, 0.38);
}
.ce-input.ce-input--disabled .ce-input__container .ce-input__date-icon svg {
  stroke: rgba(0, 0, 0, 0.38);
}
.ce-input.ce-input--disabled .ce-input__container:focus {
  box-shadow: none;
}
.ce-input.ce-input--disabled .ce-input__container:hover {
  box-shadow: none;
}
.ce-input.ce-input--disabled .ce-input__container:active {
  box-shadow: none;
}
.ce-input.ce-input--disabled .ce-input__container .ce-input__button {
  pointer-events: none;
  background: #aeaeae;
}
.ce-input.ce-input--error .ce-input__container {
  border-color: #cc0043;
}
.ce-input.ce-input--error .ce-input__container .ce-input__icon svg {
  stroke: #e5004b;
}
.ce-input.ce-input--error .ce-input__container input {
  color: #e5004b;
}
.ce-input.ce-input--error .ce-input__container .ce-input__date-icon svg {
  stroke: #e5004b;
}
.ce-input.ce-input--error .ce-input__container .ce-input__button {
  background: #cc0043;
}
.ce-input.ce-input--error .ce-input__container:focus {
  box-shadow: -0.3rem 0 #ffccdc, 0.3rem 0 #ffccdc, 0 -0.3rem #ffccdc, 0 0.3rem #ffccdc;
}
.ce-input.ce-input--error .ce-input__container:hover {
  box-shadow: -0.3rem 0 #ffe5ed, 0.3rem 0 #ffe5ed, 0 -0.3rem #ffe5ed, 0 0.3rem #ffe5ed;
}
.ce-input.ce-input--error .ce-input__container:active {
  box-shadow: -0.3rem 0 #ffb3cc, 0.3rem 0 #ffb3cc, 0 -0.3rem #ffb3cc, 0 0.3rem #ffb3cc;
}
.ce-input.ce-input--error .ce-errorbox {
  margin-left: 0.3rem;
  margin-right: 0.3rem;
  max-width: calc(100% - 0.6rem);
}
